import React, { Component } from 'react';
import './Transaction.scss';

import { Button, message } from 'antd';
import PropTypes from 'prop-types';
import Eos,{ network, test_network } from '../utils/eos';

const spread_fun = (source) => {
    var arr = []
    for (let i in source) {
        arr.push(source[i]); //属性
    }
    return arr;
}

class Transaction extends Component {
    static contextTypes = {
        store: PropTypes.object,
    };
    constructor(props){
        super(props);
        this.state = {
            result:''
        }
    }

    clear() {
        this.param_ref.value = '';
        this.setState({
            result: ''
        });
        console.log(this.param_ref.value);
    }

    send() {
        try{
            const config = JSON.parse(this.param_ref.value);
            console.log(config);
            const store = this.context.store;
            const { scatter_account, main_net } = store.getState();
            console.log(store.getState());
            if (!scatter_account) {
                message.info('请先登陆');
                return;
            }        
            this.setState({
                result: ''
            }); 
            
            // const params = spread_fun(config.params);
            const trx_option = {authorization: [`${scatter_account.name}@${scatter_account.authority}`]};
              
            
            console.log(network, test_network);
            const eos = main_net ? window.scatter.eos(network, Eos, {}) : window.scatter.eos(test_network, Eos, {});
            console.log('eos', eos);
            eos.contract(config.contract).then(contract => {
                contract[config.fn](config.params, trx_option).then((data) => {
                    console.log(data);
                    message.success('操作成功');
                    this.setState({
                        result: JSON.stringify(data)
                    });
                }).catch(e=> {
                    const msg = typeof(e) === 'string'? JSON.parse(e).error.details[0].message : e.message;
                    message.error(msg);
                    this.setState({
                        result: msg
                    });
                });             
            }).catch(e => {
                const msg = typeof(e) === 'string'? JSON.parse(e).error.details[0].message : e.message;
                message.error(msg);
                this.setState({
                    result: msg
                });
            })
        }catch(e){
            const msg = typeof(e) === 'string'? JSON.parse(e).error.details[0].message : e.message;
            message.error(String(msg));
        }
        
    }

    

    render() {
        const { result } = this.state;
        return (
            <div className="tra_wrap">
                <textarea rows="10" placeholder="输入发送data" className="param_area" ref={(el)=>{this.param_ref = el}}></textarea>
                <div className="btn_line">
                    <Button onClick={() => {this.clear()}}>清空</Button>
                    <Button type="primary" style={{marginLeft: '10px'}} onClick={()=> {this.send()}}>发送</Button>
                </div>
                <div className="result_box">
                    {result}
                </div>
            </div>
        )
    }
}

export default Transaction;