export default (state = {main_net: true}, action) => {
    const {type, payload} = action;
    let new_state = {...state, ...payload};

    switch (type) {
        case 'scatter_account':
        return new_state;
        case 'login_out':
        return new_state;
        case 'net_change':
        return new_state;
    }
    
}