import React, { Component } from 'react';
import './Header.scss';

import { message, Avatar, Dropdown, Menu, Button, Switch } from 'antd';
import {
    network,
    test_network
} from './utils/eos';
import PropTypes from 'prop-types';

class MyHeader extends Component {
    
    static contextTypes = {
        store: PropTypes.object,
    };

    constructor(props){
        super(props);
        this.state = {
            scatter_name: undefined
        }
        this.get_user_info = this.get_user_info.bind(this);
        this.login = this.login.bind(this);
    }

    componentDidMount() {       

        document.addEventListener('scatterLoaded', scatterExtension => {
            console.warn('scatter:', window.scatter);
            const scatter = window.scatter;
            this.setState({scatter}, () => {
                if (scatter.identity && scatter.identity.accounts) {
                    this.get_user_info(scatter);
                } else {
                    this.login();
                }
            });
        });
    }

    get_user_info(scatter) {
        const account = scatter.identity.accounts.find((account) => account.blockchain === 'eos');
        const trx_option = {authorization: [`${account.name}@${account.authority}`]};
        this.setState({
            scatter_name:account.name
        });
        const { store } = this.context;
        store.dispatch({
            type:'scatter_account',
            payload: {
                scatter_account: account
            }
        });
    }

    login = () => {
        if (this.state.scatter) {
            const store = this.context.store;
            const { main_net } = store.getState() || {main_net: true};
            const net = main_net ? network : test_network;
            this.state.scatter.getIdentity({ accounts: [net] }).then(() => {
                message.success('登陆成功');
                const scatter = window.scatter;
                this.setState({
                    scatter,
                }, () => {
                    if (scatter.identity && scatter.identity.accounts) {
                        this.get_user_info(scatter);
                    }
                });
            }).catch(() => {
                message.error('登陆失败，您取消了授权');
            });
        } else {
            message.warning('请安装 scatter');
        }
    }

    login_out= (change = false) =>{        
        window.scatter.forgetIdentity().then(() => {
            this.setState({
                scatter_name:undefined,
            });
            const { store } = this.context;
            
            store.dispatch({
                type:'login_out',
                payload: {
                    scatter_account: undefined
                }
            });
            if (change) {
                this.login();
            }
        });
    }

    net_change(checked) {
        
        const store = this.context.store;
        store.dispatch({
            type:'net_change',
            payload: {main_net: checked}
        });
    }

    render() {
        const { scatter_name } = this.state;        

        const menu = (
            <Menu>
                <Menu.Item key="1" onClick={() => {this.login_out(true)}}>切换用户</Menu.Item>
                <Menu.Item key="2" onClick={() => {this.login_out()}}>退出登陆</Menu.Item>
            </Menu>
        );
        return (
            <div className="header_wrap">
                <div className="header_r fr hand">
                    <Switch checkedChildren="主网" unCheckedChildren="麒麟" defaultChecked  className="net_switch" onChange={(checked) => {this.net_change(checked)}}/>
                    {scatter_name ? <Dropdown overlay={menu}>
                            <Button className="user_wrap"><Avatar icon="user" size="small"/>&nbsp;{scatter_name}</Button>
                        </Dropdown>:
                        (<span className="login" onClick={() => {this.login()}}>请登录</span>)
                    }
                </div>
            </div>
        )
    }
}

export default MyHeader;